import React from "react";

function Todo({ todo, onDelete }) {
  return (
    <div>
      <div className="container">
        <h4>{todo.title}</h4>
        <p>{todo.desc}</p>
        <button
          className="btn-sm btn-danger btn"
          onClick={() => onDelete(todo)}
        >
          Delete
        </button>
        <hr />
      </div>
    </div>
  );
}

export default Todo;
